
/* ─────────────────────────────────────────────────────────
   🎓 STUDENT TERMS - SUPREME ELITE DESIGN
   ───────────────────────────────────────────────────────── */

.student-terms-supreme {
  position: relative;
  min-height: 100vh;
  overflow-x: hidden;
  padding-bottom: var(--space-12);
  background: transparent;
}

.header-supreme {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  margin: 0;
  max-width: none;
  border-radius: 0;
}

.header-content-supreme {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-4) var(--space-12);
  background: white;
  border-bottom: 2px solid #e2e8f0;
  border-radius: 0;
  box-shadow: 0 4px 15px rgba(0,0,0,0.05);
}

.header-right-section {
  display: flex;
  align-items: center;
  gap: var(--space-6);
}

.logo-section h1 {
  font-size: 1.8rem;
  font-weight: 900;
  color: var(--primary-color);
  margin: 0;
  text-shadow: none;
}

.user-profile-supreme {
  display: flex;
  align-items: center;
  gap: var(--space-4);
}

.header-actions-supreme {
  display: flex;
  align-items: center;
}

.btn-logout-supreme {
  background: #fee2e2;
  border: 1px solid #fecaca;
  color: #ef4444;
  padding: var(--space-2) var(--space-5);
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  gap: var(--space-2);
  font-weight: 600;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-logout-supreme:hover {
  background: #fecaca;
  transform: translateY(-2px);
}

/* ─────────────────────────────────────────────────────────
   🧭 TERMS MAIN SECTION
   ───────────────────────────────────────────────────────── */

.terms-main-supreme {
  position: relative;
  z-index: 10;
  max-width: 1200px;
  margin: var(--space-32) auto var(--space-12);
  padding: 0 var(--space-8);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-header-supreme {
  text-align: center;
  margin-bottom: var(--space-16);
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title-group-supreme {
  background: white;
  padding: var(--space-8) var(--space-12);
  border-radius: var(--radius-2xl);
  border: 1px solid #e2e8f0;
  display: inline-block;
  margin-top: var(--space-12); /* إنزال العنوان تحت الهيدر بمسافة كافية */
  box-shadow: 0 10px 30px rgba(0,0,0,0.05);
}

.section-title-supreme {
  font-size: clamp(2rem, 5vw, 2.5rem);
  font-weight: 900;
  color: #0f172a;
  margin-bottom: var(--space-2);
}

.section-subtitle-supreme {
  font-size: 1.1rem;
  color: #64748b;
  font-weight: 400;
}

.terms-grid-supreme {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: var(--space-8);
}

/* 💎 TERM CARD SUPREME */
.term-card-supreme {
   background: white;
   border-radius: var(--radius-2xl);
   padding: var(--space-8);
   text-align: center;
   transition: all 0.4s var(--ease-smooth);
   border: 1px solid #e2e8f0;
   box-shadow: 0 10px 30px rgba(0,0,0,0.05);
   display: flex;
   flex-direction: column;
   gap: var(--space-6);
   cursor: pointer;
   position: relative;
   overflow: hidden;
 }

 .term-card-supreme:nth-child(odd) {
   background: linear-gradient(145deg, #ffffff, #f0f9ff); /* تدرج أزرق خفيف جداً */
 }

 .term-card-supreme:nth-child(even) {
   background: linear-gradient(145deg, #ffffff, #f5f3ff); /* تدرج بنفسجي خفيف جداً */
 }

.term-card-supreme::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 6px;
  background: linear-gradient(to right, var(--primary-color), #3b82f6);
}

.term-card-supreme:nth-child(even)::before {
  background: linear-gradient(to right, #8b5cf6, #a855f7);
}

.term-card-supreme:hover {
  transform: translateY(-10px);
  border-color: var(--primary-color);
  box-shadow: 0 20px 40px rgba(30, 64, 175, 0.15);
}

.term-card-supreme:nth-child(even):hover {
  border-color: #8b5cf6;
  box-shadow: 0 20px 40px rgba(139, 92, 246, 0.15);
}

.term-icon-section {
  width: 70px;
  height: 70px;
  background: var(--aurora-1);
  border-radius: var(--radius-2xl);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 2rem;
  box-shadow: 0 8px 16px rgba(0,0,0,0.1);
}

.term-title-supreme {
  font-size: 1.75rem;
  font-weight: 800;
  color: #0f172a;
  margin: 0;
}

.badge-item-supreme {
  padding: var(--space-1.5) var(--space-4);
  border-radius: var(--radius-full);
  font-size: 0.8rem;
  font-weight: 700;
}

.badge-item-supreme.available {
  background: #f0fdf4;
  color: #16a34a;
  border: 1px solid #bbf7d0;
}

.badge-item-supreme.subjects {
  background: #f8fafc;
  color: #64748b;
  border: 1px solid #e2e8f0;
}

.term-footer-section {
  margin-top: auto;
  padding-top: var(--space-4);
  border-top: 1px solid #f1f5f9;
}

.action-button-supreme {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--primary-color);
  font-weight: 600;
  font-size: 0.95rem;
  transition: all 0.3s;
}

.arrow-icon {
  font-size: 1.25rem;
  transition: transform 0.3s;
}

.term-card-supreme:hover .arrow-icon {
  transform: translateX(-5px);
}

/* ─────────────────────────────────────────────────────────
   👤 USER PROFILE SUPREME
   ───────────────────────────────────────────────────────── */

.user-profile-supreme {
  display: flex;
  align-items: center;
  gap: var(--space-4);
  padding: var(--space-2) var(--space-4);
  background: rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-full);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.user-avatar-supreme {
  width: 40px;
  height: 40px;
  background: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  color: var(--primary-color);
  font-size: 1.1rem;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

.user-info-supreme {
  display: flex;
  flex-direction: column;
  line-height: 1.2;
}

.welcome-label {
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 500;
}

.user-name-supreme {
  font-size: 0.95rem;
  font-weight: 700;
  color: white;
  margin: 0;
}

.user-badges-supreme {
  display: flex;
  gap: var(--space-2);
  margin-top: var(--space-1);
}

.badge-item {
  display: flex;
  align-items: center;
  gap: var(--space-1);
  background: rgba(255, 255, 255, 0.15);
  padding: 2px 8px;
  border-radius: var(--radius-full);
  font-size: 0.65rem;
  color: white;
  font-weight: 600;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.badge-item.points svg { color: #fbbf24; }
.badge-item.rank svg { color: #f87171; }

.header-actions-supreme {
  display: flex;
  align-items: center;
  gap: var(--space-3);
}

.btn-logout-supreme {
  background: rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  padding: var(--space-2) var(--space-4);
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  gap: var(--space-2);
  font-weight: 600;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-logout-supreme:hover {
  background: rgba(255, 255, 255, 0.25);
  transform: translateY(-2px);
}

/* ─────────────────────────────────────────────────────────
   Empty State
   ───────────────────────────────────────────────────────── */

.empty-state-supreme {
  max-width: 600px;
  margin: 0 auto;
  padding: var(--space-12);
  text-align: center;
  border-radius: var(--radius-3xl);
}

.empty-icon-container {
  width: 120px;
  height: 120px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto var(--space-6);
  color: rgba(255, 255, 255, 0.3);
}

.empty-state-supreme h3 {
  font-size: 1.75rem;
  color: white;
  margin-bottom: var(--space-3);
}

.empty-state-supreme p {
  color: rgba(255, 255, 255, 0.6);
  font-size: 1.125rem;
}

/* Responsive */
@media (max-width: 768px) {
  .terms-grid-supreme {
    grid-template-columns: 1fr;
  }
  
  .terms-main-supreme {
    padding: 0 var(--space-4);
  }
  
  .section-header-supreme {
    margin-bottom: var(--space-8);
  }
}
