.student-home-supreme {
  position: relative;
  min-height: 100vh;
  overflow-x: hidden;
  padding-bottom: var(--space-12);
}

.header-supreme {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  margin: 0;
  max-width: none;
  border-radius: 0;
}

.header-content-supreme {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-4) var(--space-12);
  background: white; /* تغيير الخلفية للأبيض لتطابق لوحة التحكم */
  border-bottom: 2px solid #e2e8f0;
  border-radius: 0;
  box-shadow: 0 4px 15px rgba(0,0,0,0.05);
}

.header-right-section {
  display: flex;
  align-items: center;
  gap: var(--space-6);
}

.logo-section h1 {
  font-size: 1.8rem;
  font-weight: 900;
  color: var(--primary-color); /* تغيير اللون للأزرق الداكن */
  margin: 0;
  text-shadow: none;
}

.user-profile-supreme {
  display: flex;
  align-items: center;
  gap: var(--space-4);
}

.user-avatar-supreme {
  width: 45px;
  height: 45px;
  background: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  color: var(--primary-color);
  font-size: 1.25rem;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

.user-info-supreme {
  display: flex;
  flex-direction: column;
  line-height: 1.2;
}

.welcome-label {
  font-size: 0.7rem;
  color: #64748b;
  font-weight: 500;
}

.user-name-supreme {
  font-size: 1rem;
  font-weight: 700;
  color: #0f172a;
  margin: 0;
}

.user-badges-supreme {
  display: flex;
  gap: var(--space-2);
  margin-top: var(--space-1);
}

.badge-item {
  display: flex;
  align-items: center;
  gap: var(--space-1);
  background: #f1f5f9;
  padding: 2px 8px;
  border-radius: var(--radius-full);
  font-size: 0.65rem;
  color: #475569;
  font-weight: 600;
  border: 1px solid #e2e8f0;
}

.badge-item.points svg { color: #fbbf24; }
.badge-item.rank svg { color: #f87171; }

.header-actions-supreme {
  display: flex;
  align-items: center;
  gap: var(--space-3);
}

.btn-glass-supreme {
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  color: #475569;
  padding: var(--space-2) var(--space-4);
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  gap: var(--space-2);
  font-weight: 600;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-glass-supreme:hover {
  background: #f1f5f9;
  border-color: var(--primary-color);
  color: var(--primary-color);
  transform: translateY(-2px);
}

.btn-logout-supreme {
  background: #fee2e2;
  border: 1px solid #fecaca;
  color: #ef4444;
  padding: var(--space-2) var(--space-4);
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  gap: var(--space-2);
  font-weight: 600;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-logout-supreme:hover {
  background: #fecaca;
  transform: translateY(-2px);
}

/* ─────────────────────────────────────────────────────────
   🧭 NAVIGATION & TITLES
   ───────────────────────────────────────────────────────── */

.navigation-actions {
  margin-bottom: var(--space-6);
}

.btn-back-supreme {
  display: flex;
  align-items: center;
  gap: var(--space-2);
  background: white;
  color: #1e293b;
  border: 1px solid #e2e8f0;
  padding: var(--space-2) var(--space-5);
  border-radius: var(--radius-full);
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
}

.btn-back-supreme:hover {
  background: #f8fafc;
  transform: translateX(5px);
  border-color: var(--primary-light);
}

.home-main-supreme {
  max-width: 1400px;
  margin: var(--space-32) auto 0;
  padding: 0 var(--space-8);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-header-supreme {
  text-align: center;
  margin-bottom: var(--space-12);
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.navigation-actions {
  margin-bottom: var(--space-8);
  width: 100%;
  display: flex;
  justify-content: flex-start;
}

.title-group-supreme {
  background: white;
  padding: var(--space-8) var(--space-12);
  border-radius: var(--radius-2xl);
  border: 1px solid #e2e8f0;
  display: inline-block;
  margin-top: var(--space-4);
  box-shadow: 0 10px 30px rgba(0,0,0,0.05);
}

.section-title-supreme {
  font-size: clamp(2rem, 5vw, 2.5rem);
  font-weight: 900;
  color: #0f172a;
  margin-bottom: var(--space-2);
}

.section-subtitle-supreme {
  font-size: 1.1rem;
  color: #64748b;
  font-weight: 400;
}

.subjects-grid-supreme {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: var(--space-8);
  margin-top: var(--space-8);
}

/* 💎 SUBJECT CARD SUPREME */
.subject-card-supreme {
  background-size: cover !important;
  background-position: center !important;
  background-repeat: no-repeat !important;
  border-radius: var(--radius-2xl);
  overflow: hidden;
  transition: all 0.4s var(--ease-smooth);
  border: 1px solid #e2e8f0; /* حدود خفيفة */
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  position: relative;
  min-height: 400px; /* زيادة الطول قليلاً */
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-color: #f8fafc;
  cursor: pointer;
}

.subject-card-supreme:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(0,0,0,0.2);
  border-color: var(--primary-color);
}

.card-overlay-supreme {
  position: absolute;
  inset: 0;
  background: linear-gradient(
    to top, 
    rgba(0, 0, 0, 0.9) 0%, 
    rgba(0, 0, 0, 0.4) 50%, 
    rgba(0, 0, 0, 0.1) 100% 
  );
  z-index: 1;
  transition: all 0.3s ease;
}

.subject-card-supreme:hover .card-overlay-supreme {
  background: linear-gradient(
    to top, 
    rgba(0, 0, 0, 0.9) 0%, 
    rgba(0, 0, 0, 0.3) 40%, 
    rgba(0, 0, 0, 0.1) 100% 
  );
}

@media (max-width: 1400px) {
  .subjects-grid-supreme {
    grid-template-columns: repeat(3, 1fr); /* الحفاظ على 3 أعمدة حتى في الشاشات المتوسطة */
    gap: var(--space-4);
  }
}

@media (max-width: 1024px) {
  .subjects-grid-supreme {
    grid-template-columns: repeat(2, 1fr); /* 2 عمود للأجهزة اللوحية */
  }
}

@media (max-width: 640px) {
  .subjects-grid-supreme {
    grid-template-columns: 1fr; /* عمود واحد للموبايل الصغير */
  }
}

.card-content-supreme {
