/* Subject Detail Styles */
.subject-detail-container {
  min-height: 100vh;
  background-color: #f8f9fa;
}

.subject-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 40px;
  color: white;
}

.btn-back {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: rgba(255, 255, 255, 0.2);
  border: none;
  color: white;
  padding: 10px 20px;
  border-radius: 10px;
  cursor: pointer;
  margin-bottom: 20px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.btn-back:hover {
  background: rgba(255, 255, 255, 0.3);
}

.subject-info {
  display: flex;
  gap: 30px;
  align-items: center;
}

.subject-info img {
  width: 150px;
  height: 150px;
  border-radius: 15px;
  object-fit: cover;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
}

.subject-info h1 {
  margin: 0 0 10px 0;
  font-size: 2.5rem;
}

.subject-info p {
  margin: 0 0 20px 0;
  opacity: 0.9;
  font-size: 1.1rem;
}

.rating-section {
  display: flex;
  align-items: center;
  gap: 15px;
}

.rating-section .stars {
  display: flex;
  gap: 5px;
  font-size: 1.5rem;
}

.rating-section .stars svg {
  cursor: pointer;
  transition: all 0.2s ease;
}

.rating-section .stars svg.filled {
  fill: #ffd700;
  color: #ffd700;
}

.tabs {
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  gap: 10px;
  padding: 20px 20px 0 20px;
}

.tab {
  background: white;
  border: none;
  padding: 15px 30px;
  border-radius: 10px 10px 0 0;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 500;
  color: #666;
  transition: all 0.3s ease;
}

.tab.active {
  background: #667eea;
  color: white;
}

.tab-content {
  max-width: 1400px;
  margin: 0 auto;
  background: white;
  padding: 30px;
  min-height: 400px;
}

.pdfs-list, .exams-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.pdf-item, .exam-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background: #f8f9fa;
  border-radius: 10px;
  transition: all 0.3s ease;
}

.pdf-item:hover, .exam-item:hover {
  background: #f0f0f0;
}

.pdf-info h4, .exam-info h4 {
  margin: 0 0 5px 0;
  color: #333;
}

.pdf-info small, .exam-info small {
  color: #999;
}

.btn-download, .btn-start-exam {
  display: flex;
  align-items: center;
  gap: 8px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.3s ease;
}

.btn-download:hover, .btn-start-exam:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.images-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.image-item {
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  aspect-ratio: 1;
}

.image-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.image-item:hover img {
  transform: scale(1.1);
}

.image-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(to top, rgba(0,0,0,0.8), transparent);
  padding: 20px;
  color: white;
  transform: translateY(100%);
  transition: transform 0.3s ease;
}

.image-item:hover .image-overlay {
  transform: translateY(0);
}

.btn-delete-image-student {
  position: absolute;
  top: 10px;
  right: 10px;
  background: rgba(255, 255, 255, 0.9);
  color: #f44336;
  border: none;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 10;
  box-shadow: 0 2px 8px rgba(0,0,0,0.2);
  transition: all 0.2s ease;
}

.btn-delete-image-student:hover {
  background: #f44336;
  color: white;
  transform: scale(1.1);
}

.image-overlay h4 {
  margin: 0 0 5px 0;
}

.exam-item.locked {
  opacity: 0.6;
}

.locked-exam {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #999;
}

.passed-icon {
  color: #4caf50;
  margin-right: 8px;
}

.exam-score {
  color: #667eea;
  font-weight: 600;
  margin-top: 5px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .home-header {
    padding: 20px;
  }

  .header-content {
    flex-direction: column;
    align-items: flex-start;
