.admin-container {
  display: flex;
  min-height: 100vh;
  background: var(--mesh-aurora), #f5f7fa;
  background-attachment: fixed;
  font-family: var(--font-body);
}

.admin-sidebar {
  width: 280px;
  background: var(--ocean-deep);
  color: white;
  display: flex;
  flex-direction: column;
  position: fixed;
  height: 100vh;
  overflow-y: auto;
  box-shadow: var(--shadow-depth-2);
  z-index: 1000;
}

.sidebar-header {
  padding: var(--space-8) var(--space-5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(0, 0, 0, 0.1);
}

.sidebar-header h2 {
  margin: 0 0 var(--space-1) 0;
  font-size: var(--text-2xl);
  font-family: var(--font-display);
  font-weight: 800;
  letter-spacing: -0.5px;
}

.sidebar-header p {
  margin: 0;
  opacity: 0.8;
  font-size: var(--text-sm);
}

.sidebar-nav {
  flex: 1;
  padding: var(--space-5) 0;
}

.nav-item {
  display: flex;
  align-items: center;
  gap: var(--space-3);
  width: 100%;
  padding: var(--space-4) var(--space-6);
  background: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
  transition: all 0.3s var(--ease-smooth);
  font-size: var(--text-base);
  text-align: right;
  border-right: 4px solid transparent;
}

.nav-item:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  padding-right: var(--space-8);
}

.nav-item.active {
  background: rgba(255, 255, 255, 0.15);
  color: white;
  border-right-color: white;
  font-weight: 700;
}

/* ستايل خاص لعنصر إدارة المديرين لتمييزه */
.nav-item[data-label="إدارة المديرين"] {
  background: rgba(255, 255, 255, 0.05);
  margin-top: var(--space-3);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  font-weight: 600;
}

.nav-item[data-label="إدارة المديرين"] svg {
  color: #ffd700; /* لون ذهبي للتميز */
  filter: drop-shadow(0 0 5px rgba(255, 215, 0, 0.5));
}

.nav-item svg {
  font-size: var(--text-xl);
}

.sidebar-footer {
  padding: var(--space-5);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(0, 0, 0, 0.1);
}

.admin-info {
  display: flex;
  align-items: center;
  gap: var(--space-3);
  margin-bottom: var(--space-4);
  padding: var(--space-3);
  background: rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-lg);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.admin-avatar {
  width: 45px;
  height: 45px;
  border-radius: var(--radius-full);
  background: white;
  color: #3b82f6;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: var(--text-xl);
  font-weight: 800;
  box-shadow: var(--shadow-depth-1);
}

.admin-details {
  flex: 1;
  min-width: 0;
}

.admin-name {
  margin: 0;
  font-weight: 700;
  font-size: var(--text-sm);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.admin-email {
  display: block;
  opacity: 0.7;
  font-size: var(--text-xs);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.btn-logout {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-2);
  width: 100%;
  padding: var(--space-3);
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--radius-md);
  color: white;
  cursor: pointer;
  transition: all 0.3s var(--ease-smooth);
  font-size: var(--text-sm);
  font-weight: 600;
}

.btn-logout:hover {
  background: rgba(220, 38, 38, 0.2);
  border-color: rgba(220, 38, 38, 0.4);
  transform: translateY(-2px);
}

.btn-action {
  display: inline-flex;
  align-items: center;
  gap: var(--space-2);
  padding: var(--space-2) var(--space-4);
  border-radius: var(--radius-md);
  border: none;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s var(--ease-smooth);
  font-size: var(--text-sm);
}

.btn-action.danger {
  background-color: #fff1f2;
  color: #e11d48;
  border: 1px solid #fda4af;
}

.btn-action.danger:hover {
  background-color: #e11d48;
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(225, 29, 72, 0.3);
}

.btn-action.success {
  background-color: #f0fdf4;
  color: #16a34a;
  border: 1px solid #86efac;
}

.btn-action.success:hover {
  background-color: #16a34a;
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(22, 163, 74, 0.3);
}

.action-row {
  display: flex;
  gap: var(--space-3);
  flex-wrap: wrap;
  align-items: center;
}

.upload-section {
  display: flex;
  align-items: center;
  gap: var(--space-4);
  flex-wrap: wrap;
  margin-bottom: var(--space-5);
  padding: var(--space-4);
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: var(--radius-lg);
  border: 2px dashed #cbd5e1;
  transition: all 0.3s var(--ease-smooth);
}

.upload-section:hover {
  border-color: #6366f1;
  background-color: rgba(99, 102, 241, 0.05);
}

.btn-upload {
  display: inline-flex;
  align-items: center;
  gap: var(--space-2);
  background: var(--ocean-deep);
  color: white;
  padding: var(--space-3) var(--space-5);
  border-radius: var(--radius-md);
  cursor: pointer;
  transition: all 0.3s var(--ease-smooth);
  font-weight: 600;
  border: none;
}

.btn-upload:hover {
  transform: translateY(-2px);
  box-shadow: var(--glow-primary);
}

.admin-main {
  flex: 1;
  margin-right: 280px;
  padding: var(--space-8);
  overflow-x: hidden;
}

.page-header {
  margin-bottom: var(--space-8);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: var(--space-5);
}

.page-header h1 {
  margin: 0;
  font-size: var(--text-4xl);
  font-family: var(--font-display);
  font-weight: 900;
  color: #1e293b;
  letter-spacing: -1px;
}

.page-header p {
  margin: var(--space-1) 0 0 0;
  color: #64748b;
  font-size: var(--text-lg);
}

.header-actions {
  display: flex;
  gap: var(--space-3);
}

.content-card {
  background: white;
  border-radius: var(--radius-xl);
  padding: var(--space-6);
  box-shadow: var(--shadow-depth-1);
  margin-bottom: var(--space-5);
  border: 1px solid rgba(0, 0, 0, 0.05);
  transition: all 0.3s var(--ease-smooth);
}

.content-card:hover {
  box-shadow: var(--shadow-depth-2);
}

.filters-section {
  display: flex;
  gap: var(--space-4);
  margin-bottom: var(--space-6);
  flex-wrap: wrap;
}

.search-box {
  flex: 1;
  min-width: 250px;
}

.search-box input {
  width: 100%;
  padding: var(--space-3) var(--space-4);
  border: 2px solid #e2e8f0;
  border-radius: var(--radius-lg);
  font-size: var(--text-base);
  transition: all 0.3s var(--ease-smooth);
  background: #f8fafc;
}

.search-box input:focus {
  outline: none;
  border-color: #3b82f6;
  background: white;
  box-shadow: 0 0 0 4px rgba(59, 130, 246, 0.1);
}

.filter-group {
  display: flex;
  align-items: center;
  gap: var(--space-2);
}

.filter-group label {
  font-weight: 600;
  color: #475569;
  font-size: var(--text-sm);
}

.filter-group select {
  padding: var(--space-2) var(--space-4);
  border: 2px solid #e2e8f0;
  border-radius: var(--radius-lg);
  font-size: var(--text-sm);
  cursor: pointer;
  background: #f8fafc;
  transition: all 0.3s var(--ease-smooth);
}

.filter-group select:focus {
  border-color: #3b82f6;
  outline: none;
}

.btn-primary {
  background: var(--ocean-deep);
  color: white;
  border: none;
  padding: var(--space-3) var(--space-6);
  border-radius: var(--radius-lg);
  font-size: var(--text-base);
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s var(--ease-smooth);
  display: inline-flex;
  align-items: center;
  gap: var(--space-2);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--glow-primary);
}

.btn-primary:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

.btn-secondary {
  background: #f1f5f9;
  color: #475569;
  border: 1px solid #e2e8f0;
  padding: var(--space-3) var(--space-6);
  border-radius: var(--radius-lg);
  font-size: var(--text-base);
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s var(--ease-smooth);
}

.btn-secondary:hover {
  background: #e2e8f0;
  color: #1e293b;
}

.data-table-container {
  overflow-x: auto;
  border-radius: var(--radius-lg);
  border: 1px solid #e2e8f0;
}

.data-table {
  width: 100%;
  border-collapse: collapse;
  background: white;
}

.data-table thead {
  background: #f8fafc;
}

.data-table th {
  padding: var(--space-4);
  text-align: right;
  font-weight: 700;
  color: #1e293b;
  border-bottom: 2px solid #e2e8f0;
  font-size: var(--text-sm);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.data-table td {
  padding: var(--space-4);
  border-bottom: 1px solid #f1f5f9;
  color: #475569;
  font-size: var(--text-sm);
}

.data-table tr:hover {
  background: #f8fafc;
}

.action-buttons {
  display: flex;
  gap: var(--space-2);
  justify-content: flex-start;
}

.btn-icon {
  display: inline-flex;
  align-items: center;
  gap: var(--space-1);
  padding: var(--space-2) var(--space-3);
  border: none;
  border-radius: var(--radius-md);
  cursor: pointer;
  font-size: var(--text-xs);
  font-weight: 700;
  transition: all 0.2s var(--ease-smooth);
}

.btn-icon.edit {
  background: #eff6ff;
  color: #2563eb;
}

.btn-icon.edit:hover {
  background: #2563eb;
  color: white;
}

.btn-icon.delete {
  background: #fff1f2;
  color: #e11d48;
}

.btn-icon.delete:hover {
  background: #e11d48;
  color: white;
}

.btn-icon.delete-overlay {
  background: white;
  color: #e11d48;
  padding: var(--space-2);
  border-radius: var(--radius-full);
  box-shadow: var(--shadow-depth-1);
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
  transition: all 0.2s var(--ease-smooth);
}

.btn-icon.delete-overlay:hover {
  background: #e11d48;
  color: white;
  transform: scale(1.1);
}

.btn-icon.view {
  background: #f5f3ff;
  color: #7c3aed;
}

.btn-icon.view:hover {
  background: #7c3aed;
  color: white;
}

.empty-state {
  text-align: center;
  padding: var(--space-12) var(--space-5);
  color: #94a3b8;
}

.empty-state h3 {
  margin: 0 0 var(--space-2) 0;
  color: #475569;
  font-family: var(--font-display);
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--space-5);
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: var(--space-2);
}

.form-group.full-width {
  grid-column: 1 / -1;
}

.form-group label {
  font-weight: 700;
  color: #1e293b;
  font-size: var(--text-sm);
  font-family: var(--font-display);
}

.form-group input,
.form-group textarea,
.form-group select {
  padding: var(--space-3) var(--space-4);
  border: 2px solid #e2e8f0;
  border-radius: var(--radius-lg);
  font-size: var(--text-base);
  font-family: var(--font-body);
  transition: all 0.3s var(--ease-smooth);
  background: #f8fafc;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: #3b82f6;
  background: white;
  box-shadow: 0 0 0 4px rgba(59, 130, 246, 0.1);
}

.form-group small {
  color: #64748b;
  font-size: var(--text-xs);
  margin-top: var(--space-1);
}

.form-actions {
  display: flex;
  gap: var(--space-4);
  margin-top: var(--space-6);
  padding-top: var(--space-6);
  border-top: 1px solid #e2e8f0;
}

.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(15, 23, 42, 0.6);
  backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  padding: var(--space-5);
}

.modal-content {
  background: white;
  border-radius: var(--radius-2xl);
  padding: var(--space-8);
  max-width: 500px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: var(--shadow-depth-3);
  position: relative;
}

.modal-content h2 {
  margin: 0 0 var(--space-5) 0;
  color: #1e293b;
  font-family: var(--font-display);
  font-weight: 800;
}

.modal-actions {
  display: flex;
  gap: var(--space-3);
  margin-top: var(--space-5);
  justify-content: flex-end;
}

.tabs {
  display: flex;
  gap: var(--space-2);
  margin-bottom: var(--space-6);
  border-bottom: 2px solid #f1f5f9;
}

.tab {
  padding: var(--space-3) var(--space-6);
  background: transparent;
  border: none;
  border-bottom: 3px solid transparent;
  color: #64748b;
  cursor: pointer;
  font-size: var(--text-base);
  font-weight: 700;
  transition: all 0.3s var(--ease-smooth);
}

.tab.active {
  color: #3b82f6;
  border-bottom-color: #3b82f6;
}

.subjects-admin-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: var(--space-6);
}

.subject-admin-card {
  background: white;
  border-radius: var(--radius-xl);
  overflow: hidden;
  box-shadow: var(--shadow-depth-1);
  transition: all 0.3s var(--ease-smooth);
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.subject-admin-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-depth-2);
}

.subject-admin-card img {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.subject-admin-info {
  padding: var(--space-5);
}

.subject-admin-info h3 {
  margin: 0 0 var(--space-1) 0;
  color: #1e293b;
  font-family: var(--font-display);
  font-weight: 700;
}

.subject-term {
  color: #3b82f6;
  font-size: var(--text-sm);
  margin-bottom: var(--space-3);
  font-weight: 700;
}

.subject-desc {
  color: #475569;
  font-size: var(--text-sm);
  margin-bottom: var(--space-4);
  line-height: 1.6;
}

.subject-admin-stats {
  display: flex;
  gap: var(--space-4);
  margin-bottom: var(--space-4);
  flex-wrap: wrap;
}

.subject-admin-stats span {
  font-size: var(--text-xs);
  color: #94a3b8;
  display: flex;
  align-items: center;
  gap: var(--space-1);
}

.subject-admin-actions {
  display: flex;
  gap: var(--space-2);
  padding-top: var(--space-4);
  border-top: 1px solid #f1f5f9;
}

.images-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: var(--space-5);
  margin-top: var(--space-5);
}

.image-card-container {
  display: flex;
  flex-direction: column;
  gap: var(--space-2);
  background: white;
  padding: var(--space-2);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-depth-1);
  transition: all 0.3s var(--ease-smooth);
  border: 1px solid #f1f5f9;
}

.image-card-container:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-depth-2);
}

.image-card {
  position: relative;
  width: 100%;
  aspect-ratio: 4/3;
  border-radius: var(--radius-md);
  overflow: hidden;
  background: #f8fafc;
}

.image-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-overlay-simple {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: var(--space-2);
  background: rgba(15, 23, 42, 0.8);
  color: white;
}

.image-overlay-simple p {
  margin: 0;
  font-size: var(--text-xs);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.btn-delete-single-image {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-2);
  width: 100%;
  padding: var(--space-2);
  background-color: #fff1f2;
  color: #e11d48;
  border: 1px solid #fda4af;
  border-radius: var(--radius-md);
  cursor: pointer;
  font-weight: 600;
  transition: all 0.2s var(--ease-smooth);
  font-size: var(--text-sm);
}

.btn-delete-single-image:hover {
  background-color: #e11d48;
  color: white;
}

.btn-delete-single-image svg {
  font-size: 1.1rem;
}

.exams-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.exam-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background: #f8f9fa;
  border-radius: 10px;
}

.exam-card h3 {
  margin: 0 0 5px 0;
  color: #333;
}

.exam-card p {
  margin: 0;
  color: #999;
  font-size: 0.9rem;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .admin-sidebar {
    width: 250px;
  }

  .admin-main {
    margin-right: 250px;
  }
}

@media (max-width: 768px) {
  .admin-sidebar {
    transform: translateX(-100%);
    z-index: 1000;
  }

  .admin-sidebar.mobile-open {
    transform: translateX(0);
  }

  .admin-main {
    margin-right: 0;
    padding: 15px;
  }

  .page-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .page-header h1 {
    font-size: 1.5rem;
  }

  .subjects-admin-grid {
    grid-template-columns: 1fr;
  }

  .form-grid {
    grid-template-columns: 1fr;
  }
}

/* Student Questions Admin Page Styles */
.questions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: var(--space-6);
  margin-top: var(--space-6);
}

.question-card {
  background: white;
  border-radius: var(--radius-xl);
  padding: var(--space-6);
  box-shadow: var(--shadow-depth-1);
  border: 1px solid #f1f5f9;
  display: flex;
  flex-direction: column;
  gap: var(--space-4);
  transition: all 0.3s var(--ease-smooth);
}

.question-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-depth-2);
}

.question-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: var(--space-4);
  padding-bottom: var(--space-4);
  border-bottom: 1px solid #f1f5f9;
}

.student-info-group {
  display: flex;
  flex-direction: column;
  gap: var(--space-1);
  flex: 1;
  min-width: 0;
}

.student-info, .student-email {
  display: flex;
  align-items: center;
  gap: var(--space-2);
  font-size: var(--text-sm);
  color: #475569;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.student-info span {
  font-weight: 700;
  color: #1e293b;
}

.status-badge {
  padding: var(--space-1) var(--space-3);
  border-radius: var(--radius-full);
  font-size: var(--text-xs);
  font-weight: 700;
  white-space: nowrap;
}

.status-badge.pending {
  background: #fffbeb;
  color: #d97706;
}

.status-badge.answered {
  background: #f0fdf4;
  color: #16a34a;
}

.question-content {
  flex: 1;
}

.question-text {
  display: flex;
  gap: var(--space-3);
  color: #1e293b;
  line-height: 1.6;
}

.question-text svg {
  color: #3b82f6;
  flex-shrink: 0;
  margin-top: 4px;
}

.reply-section {
  margin-top: var(--space-4);
  padding: var(--space-4);
  background: #f8fafc;
  border-radius: var(--radius-lg);
  border-right: 4px solid #3b82f6;
}

.reply-section h4 {
  margin: 0 0 var(--space-2) 0;
  font-size: var(--text-sm);
  color: #3b82f6;
}

.reply-section p {
  margin: 0 0 var(--space-2) 0;
  font-size: var(--text-sm);
  color: #475569;
}

.reply-section small {
  color: #94a3b8;
  font-size: var(--text-xs);
}

.question-actions {
  display: flex;
  gap: var(--space-3);
  margin-top: auto;
}

.btn-reply {
  flex: 1;
  background: #3b82f6;
  color: white;
  border: none;
  padding: var(--space-2) var(--space-4);
  border-radius: var(--radius-md);
  font-weight: 700;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-2);
  transition: all 0.2s;
}

.btn-reply:hover {
  background: #2563eb;
}

.btn-delete {
  background: #fff1f2;
  color: #e11d48;
  border: 1px solid #fda4af;
  padding: var(--space-2) var(--space-4);
  border-radius: var(--radius-md);
  font-weight: 700;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-2);
  transition: all 0.2s;
}

.btn-delete:hover {
  background: #e11d48;
  color: white;
}
